* {
  box-sizing: border-box;
  margin: 0;
}

:root {
  --heights: 90vh;
  --widths: 100%;
}

.slider-container {
  height: 90vh;
  height: var(--heights);
  width: 100%;
  width: var(--widths);
  position: relative;
  margin: auto;
  overflow: hidden;
}

.active {
  display: inline-block;
}

.inactive {
  display: none;
}

.slides {
  height: 90vh;
  height: var(--heights);
  width: 100%;
  width: var(--widths);
  position: relative;
}

.slide-image {
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
}

.slide-title,
.slide-text-para,
.slide-text {
  width: 100%;
  height: 100%;
  color: white;
  font-size: 60px;
  line-height: 62px;
  /* font-weight: bold; */
  position: absolute;
  text-align: center;
  top: 45%;
  /* z-index: 10; */
}

.slide-text {
  top: 57%;
  font-size: 1.5rem;
  /* z-index: 10; */
  font-weight: normal;
}

.slide-text-para {
  top: 37%;
  font-size: 1.5rem;
  /* z-index: 10; */
}
.btn {
  /* z-index: 10; */
  top: 68%;
  left: 45%;
  cursor: pointer;
  width: 140px;
  height: 40px;
  background-color: red;
  position: absolute;
  text-align: center;
  border: none;
  border-radius: 30px;
  color: white;
  font-size: 16px;
}
/* .btn {
  font-size: 20px;
  border: none;
} */
.btn :hover {
  background-color: black;
}
.prev,
.next {
  color: transparent;
  cursor: pointer;
  /* z-index: 100; */
  position: absolute;
  top: 50%;
  width: auto;
  padding: 1rem;
  margin-top: -3rem;
  font-size: 40px;
  font-weight: bold;
  border-radius: 0px 5px 5px 0px;
}

.slider-container:hover .prev,
.slider-container:hover .next {
  color: black;
}

.slider-container:hover .prev:hover,
.slider-container:hover .next:hover {
  color: white;
  background-color: rgba(0, 0, 0, 0.6);
  transition: all 0.5s ease-in;
}

.next {
  right: 0;
  border-radius: 5px 0px 0px 5px;
}

.all-dots {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  top: 85%;
  justify-content: center;
  z-index: 200;
}

.dot {
  cursor: pointer;
  height: 1.5rem;
  width: 1.5rem;
  margin: 0px 3px;
  background-color: transparent;
  /* background-color: rgba(0, 0, 0, 0.3); */
  border-radius: 50%;
  display: inline-block;
}

.slider-container:hover .dot:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

/* .active-dot {
  background-color: rgba(255, 255, 255, 0.5);
} */

.slider-container:hover .dot {
  background-color: rgba(0, 0, 0, 0.3);
}
.slider-container:hover .active-dot {
  background-color: rgba(255, 255, 255, 0.5);
}

.play-pause {
  color: black;
}

@media (min-width: 320px) and (max-width: 425px) {
  .btn {
    z-index: 10;
    top: 72%;
    left: 31%;
    cursor: pointer;
    width: 140px;
    height: 40px;
    background-color: red;
    position: absolute;
    text-align: center;
    border: none;
    border-radius: 30px;
    color: white;
    font-size: 16px;
  }
  .slide-text-para {
    top: 30%;
  }
  .slide-title {
    top: 43%;
    font-size: 39px;
  }
  .slide-text {
    top: 59%;
  }
}

